<script lang="ts">
	import { getLoadedDataForSelection } from './../stores/datasets_store';
    import type { ChartType, MultipleChartsSpec, FullChartSpec } from "../types/chart_spec";
    import LineLayer from "./layers/LineLayer.svelte";
    import SingleAxisLayer from "./layers/SingleAxisLayer.svelte";
    import { ensureDataSelectionIsLoaded } from "../stores/datasets_store";
    import { watchResize } from './gui-utils/watch-resize';
    
    // For summary we don't need a selector,
    // Maybe just pass a "*" or something like that
    export let charts: MultipleChartsSpec[];
    export let axisX: string;
    export let axisY: string;
    export let axisMarginLeft: number = 35
    export let axisMarginBottom: number = 35
    export let showAxisX: boolean = true
    export let showAxisY: boolean = true
    
    $: singleChartSpecs = (): FullChartSpec[] => {
        return charts.flatMap(({ serverURL, selection, type, style }) => {
            const ensembleStyles: Record<string, Record<string, string|number>> = {}
            Object.keys(style)
                .filter(k => k.startsWith("ensemble:"))
                .forEach(k => {
                    const ensembleAlias = k.split("ensemble:")[1]
                    
                    if (!(ensembleAlias in ensembleStyles))
                        ensembleStyles[ensembleAlias] = {}
                    
                    ensembleStyles[ensembleAlias] = { 
                        ...style[k], 
                        ... ensembleStyles[ensembleAlias]
                    }
                })
                
            const muhData = getLoadedDataForSelection(serverURL, selection)
            const { selector } = selection
            return muhData.map<FullChartSpec>(({ datasetURL, ensembleAlias }) => {
                let style = {}
                if (ensembleAlias in ensembleStyles)
                    style = { ...ensembleStyles[ensembleAlias], ...style }
                return ({ type, axisX, axisY, selector, datasetURL, style })
            })
        })
    }

    $: ensureDatasetsAreLoaded = async () => {
        await Promise.all(charts.map(({ serverURL, selection }) => ensureDataSelectionIsLoaded(serverURL, selection)))
    }

    $: domainY = (): [number, number] => {
        const domains = charts.map(({ serverURL, selection }) => getLoadedDataForSelection(serverURL, selection).domain(axisY, selection.selector))
        return [Math.min(...domains.map(d => d[0])), Math.max(...domains.map(d => d[1]))]
    }
    
    $: domainX = (): [number, number] => {
        const domains = charts.map(({ serverURL, selection }) => getLoadedDataForSelection(serverURL, selection).domain(axisX, selection.selector))
        return [Math.min(...domains.map(d => d[0])), Math.max(...domains.map(d => d[1]))]
    }

    const typeToComponentType: Record<ChartType, any> = {
        line: LineLayer,
        area: LineLayer,
        bars: LineLayer,
        dots: LineLayer
    }


    let containerWidth = 0
    let containerHeight = 0
    function handleResize(node: HTMLElement) {
        const resizeObserver = new ResizeObserver(entries => {
            for (const entry of entries) {
                const { width, height } = entry.contentRect;
                containerWidth = width;
                containerHeight = height;
            }
        });

        resizeObserver.observe(node);

        return {
            destroy() {
                resizeObserver.disconnect();
            }
        };
    }

    const onResize = (event: CustomEvent): void => {
        const { entries } = event.detail
        const entry = entries[0]
        const { width, height } = entry.contentRect;
        containerWidth = width;
        containerHeight = height;
    }
</script>

{#await ensureDatasetsAreLoaded()}
    <p>Loading datasets... pls wait</p>
{:then} 
    <div 
        class="relative w-full h-full shadow-lg rounded m-5"
        use:watchResize
        on:resized={onResize}
        >
        {#if (containerWidth > 0 && containerHeight > 0)}
            <svg class="absolute top-0 left-0 w-full h-full">
                {#if showAxisX || showAxisY}
                    <g transform={`translate(${axisMarginLeft},0)`}>
                        <SingleAxisLayer
                            width={containerWidth-axisMarginLeft}
                            height={containerHeight-axisMarginBottom}
                            domainY={domainY()}
                            domainX={domainX()}
                            axisXKey={axisX}
                            axisYKey={axisY}
                        />
                    </g>
                {/if}
                <g transform={`translate(${axisMarginLeft},0)`}>
                    {#each singleChartSpecs() as spec}
                        <svelte:component
                            this={typeToComponentType[spec.type]}
                            width={containerWidth-axisMarginLeft}
                            height={containerHeight-axisMarginBottom}
                            domainY={domainY()}
                            domainX={domainX()}
                            spec={spec}/>
                    {/each}
                </g>
            </svg>
        {/if}
    </div>
{/await}

