/**
 * Base assumption: Not really a store, doesn't have to be reactive 
 * in the sense that we assume datasets are final 
 * once we retrieve them.
 * */
import { NetCDFTableWrapper, loadNetCDFTable } from "../util/netcdf_processing"
import type { SummaryRow, DiffRow, ObservationRow, ParamRow } from "../types/ert_dataset_schemas"
import type { DataSelectionQuery } from "../types/chart_spec"

type DatasetRowType = SummaryRow | DiffRow | ObservationRow | ParamRow

const datasetsCache: Record<string,NetCDFTableWrapper<DatasetRowType>> = {}

const getDataset = async <T extends DatasetRowType> (url: string) : Promise<NetCDFTableWrapper<T>> => {
    if (!(url in datasetsCache)) {
        const table = await loadNetCDFTable<T>(url)
        datasetsCache[url] = table!
    }
    
    return datasetsCache[url] as NetCDFTableWrapper<T>
}

/**
 * Gets a dataset that is already loaded.
 * @param url 
 * @returns 
 */
export const getLoadedDataset = <T extends DatasetRowType> (url: string) : NetCDFTableWrapper<T> => {
    if (!(url in datasetsCache)) {
        throw new Error(`Dataset @ ${url} is not loaded`);
    }

    return datasetsCache[url] as NetCDFTableWrapper<T>
}

export const ensureDatasetIsLoaded = getDataset

interface EnsembleIndexJSON {
    ensemble_size: number
    experiment_id: string
    id: string
    iteration: number
    name: string
    prior_ensemble_id: string
    started_at: string
}

const _dataForSelectionCache: Record<string, {
    table: NetCDFTableWrapper<object>
    ensembleKey: string,
    ensembleAlias: string,
    realizationIndex: number,
    datasetURL: string,
}[]> = {}
export const ensureDataSelectionIsLoaded = async (serverURL: string, selection: DataSelectionQuery): Promise<void> => {
    // Now parse the selection and make sure all datasets are loaded
    const { ensembles, realizations, netcdfFile } = selection
    const ensemblesIndexResponse = await fetch(`${serverURL}/ensembles/index.json`)
    const ensemblesIndex = await ensemblesIndexResponse.json()

    const ensemblesList = ensemblesIndex.ensembles as string[]

    for (let ensembleKey of ensembles) {
        let actualEnsembleKey
        if (ensembleKey === "first") {
            actualEnsembleKey = ensemblesList[0]
        }
        else if (ensembleKey === "last") {
            actualEnsembleKey = ensemblesList[ensemblesList.length - 1]
        }
        else {
            actualEnsembleKey = ensembleKey
        }

        // Get the index.json and check it
        const ensembleURL = `${serverURL}/ensembles/${actualEnsembleKey}`
        const ensembleIndexURL = `${ensembleURL}/index.json`
        
        const ensembleIndexResponse = await fetch(ensembleIndexURL)
        const ensembleIndex = await ensembleIndexResponse.json() as EnsembleIndexJSON
        
        const { ensemble_size } = ensembleIndex
        // Now we need to load every realization

        if (!(actualEnsembleKey in _dataForSelectionCache)) {
            _dataForSelectionCache[actualEnsembleKey] = new Array(ensemble_size)
            
            if (ensembleKey !== actualEnsembleKey) {
                // We used an alias, i.e "first" or "last", they should
                // point to the same info object
                _dataForSelectionCache[ensembleKey] = _dataForSelectionCache[actualEnsembleKey]
            }
        }

        if (realizations === "*") {
            for (let realizationIndex = 0; realizationIndex < ensemble_size; ++realizationIndex) {
                const datasetURL = `${ensembleURL}/realization-${realizationIndex}/${netcdfFile}`
                await ensureDatasetIsLoaded(datasetURL)
                _dataForSelectionCache[actualEnsembleKey][realizationIndex] = {
                    ensembleKey: actualEnsembleKey,
                    ensembleAlias: ensembleKey,
                    realizationIndex,
                    datasetURL,
                    table: getLoadedDataset(datasetURL)
                }
                console.info(`${datasetURL} loaded successfully`)
            }
        }
    }
}

class DataForSelection {
    private data: DataSelectionResult[] = []

    constructor(data: DataSelectionResult[]) {
        this.data = data
    }

    _domainsCache: Record<string, [number, number]> = {}
    domain(column: string, selector?: string): [number, number] {
        const key = `col:${column};;sel${selector}`
        if (key in this._domainsCache)
            return this._domainsCache[key]

        if (!!selector) return this._domainsCache[key] = this._domainForSelector(column, selector)
        
        else {
            const domains = this.data.map(({ table }) => table.domain(column))
            return this._domainsCache[key] = [Math.min(...domains.map(d => d[0])), Math.max(...domains.map(d => d[1]))]
        }
    }

    private _domainForSelector(column: string, selector: string): [number, number] {
        const domains = this.data.map(({ table }) => table.select(selector).domain(column))
        return [Math.min(...domains.map(d => d[0])), Math.max(...domains.map(d => d[1]))]
    }

     map<T>(callbackfn: (value: DataSelectionResult, index: number, array: DataSelectionResult[]) => T) {
        return this.data.map(callbackfn)
    }
}

export interface DataSelectionResult {
    table: NetCDFTableWrapper<object>
    realizationIndex: number
    ensembleKey: string,
    ensembleAlias: string,
    datasetURL: string
}
export const getDataForSelection = async (serverURL: string, selection: DataSelectionQuery): Promise<DataForSelection> => {
    await ensureDataSelectionIsLoaded(serverURL, selection)
    return getLoadedDataForSelection(serverURL, selection)
}

export const getLoadedDataForSelection = (serverURL: string, selection: DataSelectionQuery): DataForSelection => {
    const result: DataSelectionResult[] = []

    selection.ensembles.forEach((ensembleKey: string) => {
        _dataForSelectionCache[ensembleKey].forEach(
            (info) => result.push({ ...info })
        )
    })

    return new DataForSelection(result)
}