import * as d3 from "d3"
import { NetCDFReader, type Attribute } from "netcdfjs"

/**
 * Represents a view of a tabular dataset. Needed to make charts
 * without knowing the exact underlying layout
 */
// type DatasetRows = Array<object>

export class DatasetRows {
    readonly rows: object[]

    constructor(rows: object[]) {
        this.rows = rows
    }

    _domainCache: Record<string, [number, number]> = {}
    domain(column: string): [number, number] {
        if (!(column in this._domainCache)) {
            const dm = this.rows.map((d: any) => d[column] as number)
            this._domainCache[column] = d3.extent(dm) as [number, number]
            
        }

        return this._domainCache[column]
    }
}

export class NetCDFTableWrapper<T> {
    readonly reader: NetCDFReader | null = null
    readonly data: T[]

    constructor(reader: NetCDFReader) {
        this.reader = reader
        this.data = this.parseFile()
    }

    private parseFile(): T[] {
        const { buffer } = this.reader!
        const { variables, dimensions } = this.reader!.header

        // This is only true for tabular data
        let numRows = 0
        let rowVariableIndex = -1

        variables.forEach(
            (v,i ) => {
                if ((v.size / 8) > numRows) {
                    numRows = v.size / 8
                    rowVariableIndex = i
                }
            }
        )

        const axes = variables[rowVariableIndex].dimensions.map((di) => dimensions[di])
        const sizes = axes.map(a => a.size)

        const parsedVariables: object[] = []
        let innerRepeats = 1
        const dimensionVariables = variables[rowVariableIndex]
            .dimensions
            .map((di) => variables.find((v) => v.name == dimensions[di].name))
        
        const flatIndexToIndices = (flatIndex: number, sizes: number[]) => {
            const indices = [];
            let remainingIndex = flatIndex;
        
            for (let i = sizes.length - 1; i >= 0; i--) {
                const size = sizes[i];
                const index = remainingIndex % size;
                indices[i] = index
                remainingIndex = Math.floor(remainingIndex / size);
            }
        
            return indices;
        }

        dimensionVariables.push(variables[rowVariableIndex])

        const proxyArray = Array.apply(null, new Array(numRows))
            .map((v, i) => {
                const dimensionIndices = [... flatIndexToIndices(i, sizes), i]
                const proxyObj = Object.create(null)
                // This is the most performance intensive part of the 
                // data loading. The instantiation of proxy getters
                dimensionIndices.forEach((i, di) => {
                    const { offset, type, name } = dimensionVariables[di]!
                    const attributes = dimensionVariables[di]!.attributes as Attribute[]

                    if (name === "time") {
                        const startDate = attributes
                            .find((a) => a.name === "units")!
                            .value.toString()
                            .split("days since ")[1]

                        // Maybe calendar is useful somehow?
                        // const calendar = attributes.find((a) => a["name"] === "calendar")!.value

                        const startDateUnix = new Date(startDate).getTime()
                        const oneDayUnix = 86400000

                        Object.defineProperties(proxyObj, {
                            [name]: {
                                get: () => startDateUnix + oneDayUnix * buffer.seek(offset + i * 4).readInt32(),
                                configurable: false
                            }
                        })
                    }
                    else if (type === "char") {
                        Object.defineProperties(proxyObj, {
                            [name]: {
                                get: () => buffer.seek(offset + i * 9).readChars(9).replaceAll("\u0000", ""),
                                configurable: false
                            }
                        })
                    }
                    else if (type === "double") {
                        Object.defineProperties(proxyObj, {
                            [name]: {
                                get: () => buffer.seek(offset + i * 8).readFloat64(),
                                configurable: false
                            }
                        })
                    }
                    else if (type === "int") {
                        Object.defineProperties(proxyObj, {
                            [name]: {
                                get: () => buffer.seek(offset + i * 4).readInt32(),
                                configurable: false
                            }
                        })
                    }
                })

                return proxyObj
            })
        
        // TODO
        // Can optimize further by NOT using proxy objects
        // but instead look up by row index also in the getters
        // Probably will do this
        return proxyArray as T[]
    }

    _selectionCache : Record<string, DatasetRows> = {}
    select(selector: string): DatasetRows {
        if (selector === "*") {
            this._selectionCache["*"] = new DatasetRows(this.data as object[])
        }
        
        if (!(selector in this._selectionCache)) {
            const filters = 
                selector
                    .split(/\s*((?:\&\&|\|\|))\s*/)
                    .map((t) => {
                        if (t == "||" || t == "&&") {
                            return t
                        }
                        
                        if (t.includes(">=")) {
                            const [left, right] = t.split(">=")
                            return (d: any) => d[left] >= right
                        }
                        else if (t.includes("<=")) {
                            const [left, right] = t.split("<=")
                            return (d: any) => d[left] <= right
                        }
                        else if (t.includes(">")) {
                            const [left, right] = t.split(">")
                            return (d: any) => d[left] >= right
                        }
                        else if (t.includes("<")) {
                            const [left, right] = t.split("<")
                            return (d: any) => d[left] <= right
                        }
                        else if(t.includes("===")) {
                            const [left, right] = t.split("===")
                            return (d: any) => d[left] === right
                        }

                        throw new Error(`Invalid selector term: ${t}, expected one of [">=", "<=", ">", "<", "==="]`)
                    })

            const filterFn = (d: any) => eval(filters.map((t) => (typeof t === "string") ? t : t(d)).join(""))
            
            const filtered = this.data.filter(filterFn)
            this._selectionCache[selector] = new DatasetRows(filtered as object[])
        }

        return this._selectionCache[selector]
    }

    domain(column: string) : [number, number] {
        const dm = this.data.map((d: any) => d[column] as number)
        return d3.extent(dm) as [number, number]
    }

    domainForSelections(column: string, selectors: string[]): [number, number] {
        const domains = selectors.map((sel) => this.select(sel).domain(column))
        const mins = domains.map((d) => d[0])
        const maxs = domains.map((d) => d[1])
        
        return [Math.min(...mins), Math.max(...maxs)]
    }
}


export const loadNetCDFTable = async <T> (url: string): Promise<NetCDFTableWrapper<T>|undefined> => {
    const response = await fetch(url)

    if (!response.ok) {
        throw new Error(`Failed to fetch dataset @ ${url}, status=${response.status} -- ${response.statusText}`)
    }

    const buffer = await response.arrayBuffer()
    const reader = new NetCDFReader(buffer)
    return new NetCDFTableWrapper<T>(reader)
}

export const loadNetCDFTables = (urls: string[]) => {
    return Promise.all(urls.map(loadNetCDFTable))
}
