<script lang="ts">
    import type { FullChartSpec } from "../../types/chart_spec";    
    export let spec: FullChartSpec
    export let width: number
    export let height: number
    export let domainY: [number, number]
    export let domainX: [number, number]
    import { ensureDatasetIsLoaded, getLoadedDataset } from "../../stores/datasets_store";
    import * as d3 from "d3";

    $: compute = (): string => {
        const dataset = getLoadedDataset(spec.datasetURL)
        const selection = dataset.select(spec.selector);
        
        const scaleX = d3.scaleLinear()
                         .domain(domainX)
                         .range([0, width])

        const scaleY = d3.scaleLinear()
                         .domain(domainY)
                         .range([height, 0])

        const lineGenerator = d3.line()
            .x((d: any) => scaleX(d[spec.axisX]))
            .y((d: any) => scaleY(d[spec.axisY]))

        // Note: D3 type declaration from @types/d3 is incorrect,
        // it should also accept any[]
        // @ts-ignore
        const lineDataString = lineGenerator(selection.rows);
        
        if (!lineDataString) {
            throw new Error("Failed to create line from data")
        }

        return lineDataString
    }
</script>

<g>
    {#await ensureDatasetIsLoaded(spec.datasetURL)}
    {:then}
        <path class="fill-none" {... spec.style } d={compute()}></path>
    {:catch error}
        <text>Failed to load dataset {error.toString()}</text>
    {/await}
</g>
